
import { Personage, Saveuser } from "@/api/index";
import dateFormat from 'vux/src/tools/date/format.js'

export default {
data() {
return {

user: [],
bookss: [],
books: [],
values: "",
Blength: 0,
pushCount: [],
Number: true,
delay: true,
mask: false,
newbook: [],

text2: false,
text3: false
};
},
methods: {
bookChang(el) {
if (el.readCount == 0) {
el.type = 0;
}
},


















bblur() {
window.scroll(0, 0);
},
cardDel(i) {
this.books.splice(i, 1);
},
cardBook() {
this.books.push({});
},
record() {
this.pass = true;







if (!this.user.name) {
this.pass = false;
this.text2 = true;
return setTimeout(() => {
this.text2 = false;
}, 1500);
}
this.books.forEach(el => {
el.readCount = el.readCount * 1;
el.readTotal = 0;
var arr = Object.keys(el);
if (arr.length < 2) {
this.pass = false;
this.text3 = true;
return setTimeout(() => {
this.text3 = false;
}, 1500);
}
});
this.delay = true;
if (this.pass && this.delay) {
let that = this;
this.delay = false;
this.pushCount.volunteer = this.values;
this.pushCount.bonaStart = this.pushCount.bonaStart.replace("", "-");
this.pushCount.bonaStart = this.pushCount.bonaStart.replace("", "-");
this.pushCount.bonaStart = this.pushCount.bonaStart.replace("", "");
this.newbook = this.bookss.concat(this.books);

localStorage.setItem("userName", this.user.name);
let local = localStorage.getItem("cardPushData");
if (local) {
let data = JSON.parse(local);



data.volunteer = this.values;
localStorage.setItem("cardPushData", JSON.stringify(data));
}
delete this.pushCount.bonaTotal;
Saveuser({
books: this.newbook,
user: this.user,
pushCount: this.pushCount
}).then(res => {
if (res.code == 1) {
this.delay = true;
this.mask = true;
this.gainUser();
return setTimeout(() => {
this.mask = false;
}, 1500);
}
});
}
},
gainUser() {
Personage({}).then(res => {
if (res.code == 1) {
this.user = res.data.user;
this.bookss = res.data.books;
this.Blength = res.data.books.length + 1;
this.pushCount = res.data.pushCount;
this.values = res.data.volunteer;
}
});
}
},
created() {
this.gainUser();
},
components: {}
};



